import React, { Component } from 'react';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <h1 className="visually-hidden">App Name</h1>
        <Header />
        <Main />
        <Footer />
      </div>
    );
  }
}

export default App;



