import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import './About.css';

class About extends Component {
  render() {
    return (
      <div className="About">
        <Helmet>
          <title>About</title>
        </Helmet>
        <div>About</div>
      </div>
    );
  }
}

export default About;



