import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import './Home.css';

class Home extends Component {
  render() {
    return (
      <div className="Home">
        <Helmet>
          <title>Home</title>
        </Helmet>
        <div>Home</div>
      </div>
    );
  }
}

export default Home;



